.app__video {
  width: 100%;
  height: 100vh;
  position: relative;
}

.app__video video {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.app__video-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.app__video--circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 1px solid var(--color-white);
}
